import axios from 'axios'
import React, {useEffect, useState} from 'react';
import Coin from './components/coin';
import './App.css';


function App() {

  const [coins, setCoins] = useState([])
  const [randomCoin, setRandomCoin] = useState(1);


  useEffect(()=>{
    axios.get('https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false')
    .then((res)=>{
      setCoins(res.data)
    })
    .catch((error) => console.log(error))
  })

  const genRandomCoin = () => {
    setRandomCoin(Math.floor(Math.random() * 100))
  }


  return (
    <div>
    <div className="header">
      <h1 className='brand'><i class="fas fa-dice"></i>Random Crypto Picker</h1>
    </div>
  

  <div className='coinsContainer'>
     {coins.length !== 0 &&
       (<Coin
        key={coins[randomCoin].id}
        name={coins[randomCoin].name}
        price={coins[randomCoin].current_price}
        symbol={coins[randomCoin].symbol}
        marketCap={coins[randomCoin].market_cap}
        volume={coins[randomCoin].total_volume}
        image={coins[randomCoin].image}
        priceChange={coins[randomCoin].price_change_percentage_24h}
        />)
     }
      
       
        <button className='RandomCoinBTN' onClick={genRandomCoin}>Generate Random Coin</button>
        </div>
         
        
        


</div>
  )
}

export default App;